<template>
  <div class="feedback0">
    <div class="feedback">
      <div class="banner">
        <div></div>
        <div>
          <h1 style="font-family: 'Exo 2', sans-serif;">Обратная связь</h1>
          <p style="font-family: 'Roboto', sans-serif;">Какой-то текст.</p>
        </div>
      </div>
      <div class="other">
        <form>
          <p>ИМЯ</p>
          <input v-model="name" placeholder="Введите имя">
          <p>EMAIL</p>
          <input v-model="email" placeholder="Введите email">
          <p>ТЕКСТ СООБЩЕНИЯ</p>
          <textarea v-model="message" placeholder="Введите текст сообщения"></textarea>
          <button>ОТПРАВИТЬ</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import {gmapApi} from 'vue2-google-maps'

export default {
  name: 'Contacts',
  data () {
    return {
      name: '',
      message: '',
    }
  },
  mounted () {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.feedback0{
  background-image: url("../assets/common-banner2.jpg");
  background-position: 50% 0;
  background-size: 125% auto;
  background-repeat: no-repeat;
}
.feedback{
  padding: 70px 0 0 0;
  color:white;
  max-width: 1000px;
  margin: 150px auto 0;
  width: 100%;
}
.banner{
  display: grid;
  grid-template-columns: auto max-content;
  margin-bottom: 90px;
}
.banner p, .banner h1{
  text-align: left;
  padding-left: 30px;
}
.other{
  color: black;
  border: rgb(202, 201, 201) 1px solid;
  border-radius: 5px;
  padding: 40px 50px;
  max-width: 600px;
  margin: 150px auto 50px;
  text-align: left;
}
input, textarea{
  border: rgb(202, 201, 201) 1px solid;
  padding: 7px 12px;
  width: 100%;
  margin-bottom: 20px;
}
button{
  padding: 10px 30px;
  background:#007CB7;
  color: white;
}
</style>



