<template>
  <div class="contacts0">
    <div class="contacts">
      <div class="banner">
        <div></div>
        <div>
          <h1 style="font-family: 'Exo 2', sans-serif;">Контакты</h1>
          <p style="font-family: 'Roboto', sans-serif;">Какой-то текст.</p>
        </div>
      </div>
      <div class="other">
        <GmapMap ref="mapRef"
          :center="{lat:45.038935, lng:38.985288}"
          :zoom="11"
          map-type-id="roadmap"
          style="width: 700px; height: 400px;z-index: 0;margin-top:50px;margin-left:-100px;"
        >
          <GmapMarker ref="myMarker"
            :position="google && new google.maps.LatLng(45.058801, 38.962732)"
            @click="infoWinOpen=!infoWinOpen"
          />

          <gmap-info-window
            :position="google && new google.maps.LatLng(45.058801, 38.962732)"
            :opened="infoWinOpen"
            @closeclick="infoWinOpen=false"
            :options="{ pixelOffset: {
                width: 0,
                height: -35
            } }"
          >
            <div v-html="infoContent"></div>
          </gmap-info-window>
        </GmapMap>
        <div class="contacts-info">
          <h4>Наш адрес:</h4>
          <p>ул. Пушкина, д.1</p>
          <h4>Режим работы:</h4>
          <p>с 9 до 18 без перерывов и выходных</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import {gmapApi} from 'vue2-google-maps'

export default {
  name: 'Contacts',
  data () {
    return {
      infoContent: 'Офис ЮСО<br>ул.Пушкина, д.1',
      infoWinOpen: true,
    }
  },
  created(){
    const path = `http://localhost:5000/api/contacts`
      axios.get(path)
      .then(response => {
        console.log(response.data)
      })
      .catch(error => {
        console.log(error)
      })
  },
  computed: {
    google: gmapApi
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.contacts0{
  background-image: url("../assets/common-banner2.jpg");
  background-position: 50% 0;
  background-size: 125% auto;
  background-repeat: no-repeat;
}
.contacts{
  padding: 70px 0 0 0;
  color:white;
  max-width: 1000px;
  margin: 150px auto 0;
  width: 100%;
}
.banner{
  display: grid;
  grid-template-columns: auto max-content;
  margin-bottom: 90px;
}
.banner p, .banner h1{
  text-align: left;
  padding-left: 30px;
}
.other{
  color: black;
  padding: 50px;
  margin: 0;
  display: grid;
  grid-template-columns: max-content auto;
}
.contacts-info{
  margin-top: 50px;
  margin-bottom: 50px;
  text-align: left;
  padding-left: 80px;
}
</style>



